* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #000;
  color: #fff;
  font-family: 'Segoe UI', 'Arial', sans-serif;
  overflow-x: hidden;
}

.app {
  min-height: 100vh;
  position: relative;
}

/* Background Effects */
.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

.stars {
  background: radial-gradient(circle at 20% 20%, rgba(0, 150, 255, 0.3) 0%, transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(0, 255, 255, 0.2) 0%, transparent 50%),
              radial-gradient(circle at 40% 60%, rgba(255, 255, 255, 0.1) 0%, transparent 50%);
  height: 100%;
}

.grid {
  background-image: linear-gradient(rgba(0, 150, 255, 0.1) 1px, transparent 1px),
                    linear-gradient(90deg, rgba(0, 150, 255, 0.1) 1px, transparent 1px);
  background-size: 50px 50px;
  height: 100%;
  opacity: 0.3;
}

/* Header */
.header {
  text-align: center;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0, 150, 255, 0.3);
}

.header h1 {
  font-size: 3.5rem;
  background: linear-gradient(45deg, #00ffff, #0096ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.header p {
  color: #888;
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.connection-status {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  border: 1px solid;
}

.connection-status.connected {
  background: rgba(0, 255, 0, 0.1);
  border-color: #00ff00;
  color: #00ff00;
}

.connection-status.disconnected {
  background: rgba(255, 0, 0, 0.1);
  border-color: #ff0000;
  color: #ff0000;
}

/* Main Interface */
.main {
  padding: 2rem;
}

.interface-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  max-width: 1400px;
  margin: 0 auto;
}

/* Voice Visualizer */
.visualizer-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.voice-visualizer {
  position: relative;
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.visualizer-core {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: radial-gradient(circle, #333, #111);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.visualizer-icon {
  font-size: 3rem;
}

.visualizer-ring {
  position: absolute;
  border: 2px solid transparent;
  border-radius: 50%;
  border-top-color: rgba(0, 150, 255, 0.6);
}

.ring-1 {
  width: 200px;
  height: 200px;
}

.ring-2 {
  width: 250px;
  height: 250px;
  border-top-color: rgba(0, 255, 255, 0.4);
}

/* Controls */
.controls {
  text-align: center;
}

.mic-button {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(45deg, #00ff00, #00cc00);
  color: white;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 1rem;
}

.mic-button:hover:not(:disabled) {
  transform: scale(1.1);
}

.mic-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.mic-button.listening {
  background: linear-gradient(45deg, #ff4444, #cc0000);
  animation: pulse 1.5s infinite;
}

.mic-button.speaking {
  background: linear-gradient(45deg, #0096ff, #0066ff);
  animation: glow 0.8s infinite alternate;
}

@keyframes pulse {
  0%, 100% { box-shadow: 0 0 20px rgba(255, 68, 68, 0.5); }
  50% { box-shadow: 0 0 40px rgba(255, 68, 68, 0.8); }
}

@keyframes glow {
  0% { box-shadow: 0 0 20px rgba(0, 150, 255, 0.5); }
  100% { box-shadow: 0 0 30px rgba(0, 150, 255, 0.8); }
}

.status {
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Chat Section */
.chat-section {
  display: flex;
  flex-direction: column;
  height: 600px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.chat-header h3 {
  color: #00ffff;
  margin: 0;
}

.clear-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  cursor: pointer;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
}

.message {
  margin-bottom: 1rem;
  padding: 1rem;
  border-radius: 15px;
  border: 1px solid;
}

.message.user {
  background: linear-gradient(135deg, rgba(0, 255, 0, 0.1), rgba(0, 200, 0, 0.05));
  border-color: #00ff00;
  margin-left: 2rem;
}

.message.assistant {
  background: linear-gradient(135deg, rgba(0, 150, 255, 0.1), rgba(0, 100, 200, 0.05));
  border-color: #0096ff;
  margin-right: 2rem;
}

.message.system {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(200, 200, 200, 0.05));
  border-color: #666;
  text-align: center;
  margin: 0.5rem 1rem;
}

.message-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 0.8rem;
  opacity: 0.7;
}

.message-text {
  line-height: 1.5;
}

/* Text Input */
.text-input-section {
  display: flex;
  padding: 1rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  gap: 0.5rem;
}

.text-input {
  flex: 1;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  padding: 0.8rem 1.5rem;
  color: #fff;
  font-size: 1rem;
}

.text-input::placeholder {
  color: #888;
}

.text-input:focus {
  outline: none;
  border-color: #0096ff;
  box-shadow: 0 0 10px rgba(0, 150, 255, 0.3);
}

.send-btn {
  background: linear-gradient(45deg, #0096ff, #0066ff);
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.send-btn:hover:not(:disabled) {
  transform: scale(1.1);
}

.send-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Initialize Button */
.init-button {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: linear-gradient(45deg, #0096ff, #0066ff);
  border: none;
  border-radius: 25px;
  padding: 1rem 2rem;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 5px 20px rgba(0, 150, 255, 0.3);
  transition: all 0.3s ease;
}

.init-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 150, 255, 0.4);
}

.init-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Responsive Design */
@media (max-width: 768px) {
  .interface-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .header h1 {
    font-size: 2.5rem;
  }
  
  .voice-visualizer {
    width: 250px;
    height: 250px;
  }
  
  .visualizer-core {
    width: 120px;
    height: 120px;
  }
  
  .chat-section {
    height: 400px;
  }
}
